:root {
  --max-width: 900px;
  --color-golden-brown: #99621e;
  --color-raw-slenna: #d38b5d;
  --color-lemon-yellow: #f3ffb6;
  --color-russian-green: #739E82;
  --color-hunter-green: #2C5530;
  --font-family: "SF UI Text", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  --font-size-xxl: 1.5rem;
  --font-size-xl: 1.25rem;
  --font-size-l: 1.125rem;
  --font-size-m: 1rem;
  --font-size-s: 0.875rem;
  --font-size-xs: 0.75rem;
  --space-xxxxl: 4rem;
  --space-xxxl: 2rem;
  --space-xxl: 1.5rem;
  --space-xl: 1.25rem;
  --space-l: 1rem;
  --space-m: 0.75rem;
  --space-s: 0.5715rem;
  --space-xs: 0.375rem;
  --space-xxs: 0.25rem;
  --space-xxxs: 0.125rem;
}

@media (min-width: 1281px) {
  html, body {
    font-size: 14px;
    text-shadow: 0.25px 0.2px #0a0a0a;
  }
  .asset-info { 
    width: 50%;
  }
}

@media (max-width: 1280px) {
  html, body {
    font-size: 14px;
    text-shadow: 0.25px p.2px #0a0a0a;
  }
  .asset-info { 
    width: 50%;
  }
}

@media (max-width: 1024px) {
  html, body {
    font-size: 13px;
    text-shadow: 0.25px 0.2px #0a0a0a;
  }
  .asset-info { 
    width: 100%;
  }
}

@font-face {
  font-family: "Irish";
  font-style: normal;
	font-weight: 1900;
  src: url(./assets/fonts/IrishGrover.ttf);
}

.Logo-label {
  font-size: 3.2rem !important;
  font-weight: 500 !important;
  margin: 15px !important;
  font-family: 'Irish';
  width: 100%;
  text-align: center !important;
  color:rgb(4, 131, 9);
  text-shadow: 1px 1.35px #0a0a0a;
}

.site-font {
  font-family: 'Irish';
}

h1, h2, h3 {
  line-height: 1;
  text-align: center;
  margin: var(--space-l) auto;
  font-family: 'Irish';
  font-size: 2.0rem !important;
  font-weight: 500 !important;
  color:rgb(4, 131, 9);
  text-shadow: 1px 1.35px #0a0a0a;
}

/* Header */
.site-header {
  padding: var(--space-xxxl) var(--space-xxl);
  width: 100%;
  max-width: var(--max-width);
}

.site-header-inner {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  padding: var(--space-xxl) var(--space-xl);
  margin: 0 auto;
  border-radius: 4px;
}

.header-address-info {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.header-address-info .header-amount {
  line-height: 1;
  padding: var(--space-s);
  background: rgb(255, 255, 245);
  border: 1px solid;
  border-radius: 4px;
  width: 90%;
  text-shadow: 2px 2px #0a0a0a;
}

.header-address-info span,
.header-address-info .disconnect-button {
  display: inline-flex;
  margin: 0 0 0 var(--space-s);
}

.header-address-info .header-account {
  line-height: 1;
  background: linear-gradient(159deg, rgb(52, 255, 33) 0%, rgb(49, 172, 100) 60%);
  padding: var(--space-s);
  border: 1px solid;
  border-radius: 4px;
}

.header-address-info .disconnect-button span {
  margin: 0;
}

.disconnect-button {
  background: linear-gradient(159deg, rgb(247, 248, 246) 0%, rgb(182, 190, 186) 60%);
}

.form-button {
  background: linear-gradient(159deg, rgb(52, 255, 33) 0%, rgb(49, 172, 100) 60%);
  border: 1px solid black !important;
  color: black !important;
  font-size: var(--font-size-l) !important;
  border-radius: 4px !important;
}

.network_select {
  color: black;
  font-weight: 1600;
  transition: color 0.3s;
}

.wallet-button {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  height: auto;
  font-size: var(--font-size-l);
  padding: var(--space-s) var(--space-m);
  margin-bottom: var(--space-xxl);
}

.wallet-icon {
  max-width: 1.8rem;
  margin-right: var(--space-s);
}

.site-layout {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background-image: url("./assets/images/body_background.png");
  background-size: 100% auto;
	background-repeat: no-repeat;
	background-attachment: fixed;
}

.site-body {
  flex: 1 0 auto;
  width: 100%;
  max-width: var(--max-width);
  padding: 0 var(--space-xxl);
}

.site-body-inner {
  padding: var(--space-xxl) var(--space-xl);
  border-radius: 4px;
  box-shadow: 0 0px 0px rgba(212, 209, 209,0.6);
  margin: 0 auto;
  text-align: center;
  min-height: 0px;
}

.site-body-empty {
  height: 0px;
}

.asset-row {
  justify-content: space-between;
  max-width: 100%;
  padding: var(--space-m);
  margin: auto;
}

.row-title { 
  font-size: 18px;
}

.asset-info { 
  margin: 0 auto;
  text-align: center;
  background: linear-gradient(159deg, rgb(23, 127, 13) 0%, rgb(2, 170, 38) 60%);
  color: gold;
  font-weight: 600;
  font-size: var(--font-size-l);
  border-radius: 4px;
  border: 1px solid rgb(196, 148, 27);
  padding: 2px;
}

.pool-info { 
  margin: 0 auto;
  text-align: center;
  background: linear-gradient(180deg, rgba(245, 246, 245, 0.581) 0%, rgba(171, 180, 175, 0.581) 60%);
  color: Black;
  font-weight: 600;
  font-size: var(--font-size-l);
  border-radius: 4px;
  border: 1px solid black;
  width: 100%;
  padding: 5px;
}

.marquee {
  margin: 0 auto;
  width: 100%;
  height: 30px;
  background: linear-gradient(120deg, rgb(70, 57, 249) 0%, rgb(0, 41, 223) 70%);
  color: white;
  border-radius: 4px;
  border: 1px solid black;
  white-space: nowrap;
  overflow: hidden;
  box-sizing: border-box;
  position: relative;
}
.marquee:before,
.marquee:after {
  position: absolute;
  top: 0;
  width: 50px;
  height: 30px;
  content: "";
  z-index: 1;
}
.marquee:before {
  left: 0;
}
.marquee:after {
  right: 0;
}
.marquee__content {
  width: 300%;
  display: flex;
  line-height: 30px;
  animation: marquee 10s linear infinite forwards;
}
.marquee__content:hover {
  animation-play-state: paused;
}
.list-inline {
  display: flex;
  justify-content: space-around;
  width: 33.33%;
  /* reset list */
  list-style: none;
  padding: 0;
  margin: 0;
}
@keyframes marquee {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-66.6%);
  }
}

.asset-button { 
  margin: 0 auto;
  text-align: center;
  padding: 2px;
}

.t_Button {
  background: linear-gradient(180deg, rgba(245, 216, 50, 0.993) 0%, rgba(255, 234, 1, 0.533) 70%);
  border-radius: 4px;
  border: 1px solid gold;
  padding: 7px;
}

.t_Button:hover {
  background-color: #997f1f; /* Green */
  color: white;
}

.asset-info span {
  margin-left: var(--space-xs);
}

.footer {
  text-align: center;
  padding-top: 10px;
}

.footer a {
  width: 100%;
  color: black;
  font-weight: 600;
  transition: color 0.3s;
}

.footer a:hover {
  color: transparent;
  background: linear-gradient(159deg, rgba(51,49,213,1) 0%, rgba(253,45,170,1) 50%);
  background-clip: text;
  -webkit-background-clip: text;
}

.icon {
  width: 20px;
  height: 20px;
}

.loading-icon {
  display: block;
  position: relative;
  width: 2rem;
  height: 2rem;
  margin: var(--space-xxxl) auto;
}

.loading-icon span {
    box-sizing: border-box;
    content: "";
    position: absolute;
    width: 2.5rem;
    height: 2.5rem;
    top: 0;
    left: 0;
    border: 4px solid #aacc99;
    border-radius: 50%;
    border-top: 4px solid var(--color-hunter-green);
    -webkit-animation: spin 2s linear infinite; /* Safari */
    animation: spin 2s linear infinite;
}

/* Safari */
@-webkit-keyframes spin {
  0% {
      -webkit-transform: rotate(0deg);
  }
  100% {
      -webkit-transform: rotate(360deg);
  }
}
@keyframes spin {
  0% {
      transform: rotate(0deg);
  }
  100% {
      transform: rotate(360deg);
  }
}